.achievement {
    background-color: #F5F5FA;
    border-radius: 12px 12px 0px 0px;
    padding: 28px 20px 30px;
    &__main {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -10px;
        padding: 31px 0 26px;
    }
    &__heading {
        font-weight: bold;
        font-size: 14px;
        line-height: 17px;
        text-align: center;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: #323264;
    }
    &__details {
        height: 57px;
        background-color: #FFFFFF;
        border-radius: 12px;
        display: flex;
        justify-content: center;
        align-items: center;
        @media (min-width:992px) {
            height: 109px;
        }
    }
    &__item {
        width: 50%;
        padding: 0 10px;
        @media (min-width:768px) {
            width: 25%;
            padding: 0 20px;
        }
        &_hidden {
            display: none;
            @media (min-width:768px) {
                display: block;
            }
        }
    }
}
